.popup {
    --opacity: 0;
    --opa: .5;
    --scale: scale(0.8);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    margin: 0;
    display: grid;
    place-items: center;
    background-color: rgba(0,0,0,.5);
    opacity: var(--opacity);
    transition: opacity .2s 0s;
    pointer-events: none;
}